.btn-bubble {
  width: 250px;
  height: 250px;
  background-color: #6e75a8;
  color: #f9ab55;
  font-family: "Nunito Sans", sans-serif;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
}

.btn-bubble:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #2f1847;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.6);
}

.img-inner-bubble {
  width: 140px;
  height: 140px;
  object-fit: contain;
  margin: 8px 8px 12px 8px;
}

.label {
  width: 210px;
  text-align: center;
  align-items: center;
  font-size: 22px;
  margin: 0;
  padding: 0 0 0 15px;
}

.search-bubble {
  background-color: #6e75a8;
  color: #f9ab55;
  font-family: "Nunito Sans", sans-serif;
  width: 70%;
}

.quick-search-coins {
  width: 70px;
  height: 70px;
  margin: 5px;
}

.quick-search-coins:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.validate-coin-label h1{
  font-size: 6vmin;
}

.validate-coin-label h2{
  font-size: 4vmin;
}
.custom-btn{
    background: #f9ab55;
    color: whitesmoke;
}

.no-match{
    background: red;
    color: whitesmoke
}

.custom-btn:hover{
    background: #f58a07;
}
h1 {
  color: #f9ab55;
  font-family: "Nunito Sans", sans-serif;
  font-size: 60px;
}

.title {
  margin: 10px;
}

.btn-space {
  margin-right: 8px;
}
.margins {
  margin-right: 1.2em;
  margin-left: 0;
}
.row {
  justify-content: center;
  text-align: center;
}
.filterBtn {
  background-color: #6e75a8 !important;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.4);
  outline: none !important;
}
.filterBtn:hover,
.filterBtn:focus,
.filterBtn:active,
.filterBtn:focus-visible {
  background-color: #2d2733 !important;
  color: #f9ab55 !important;
  outline: none !important;
  box-shadow: none !important;
}

.filterMenu {
  background-color: #f7f5fb;
}
.filterItem:hover {
  background-color: #6e75a8;
  color: white;
}

.my-modal {
  display: flex;
  width: 600px; /* Occupy the 90% of the screen width */
  max-width: 600px;
  min-width: none;
  text-align: left;
}

.modalText {
  padding-left: 10px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(605px, 1fr));
  align-items: stretch;
  object-fit: cover;
  padding: 1em;
  grid-gap: 1em;
  gap: 1em;
  overflow: auto;
}

.quizBtn {
  color: white !important;
  background-color: #6e75a8 !important;
  outline: none !important;
  box-shadow: none !important;
}
.quizBtn:hover,
.quizBtn:focus .quizBtn:active,
.quizBtn:focus-visible {
  background-color: #2d2733 !important;
  color: #f9ab55 !important;
  outline: none !important;
  box-shadow: none !important;
}

.quizForm {
  padding: 10px;
  margin-left: 10px;
}
.quizModal {
  font-size: 18px;
}
.quizModal-header {
  padding: 5px 10px;
}
.quizModal-body {
  padding-left: 30px;
}

